.landing-page-v1-about-wrapper {
  display: flex;
  align-items: center;
}

.landing-page-v1-about-thumb {
  width: 50%;
  display: flex;
  align-items: flex-end;
  margin-right: 70px;
  margin-bottom: 125px;
  // .image-02 {
  //     margin-left: -225px;
  //     margin-bottom: -125px;
  //     position: relative;
  //     .play-button {
